.footer {
  background: #f7f6f3;
  padding: 48px 0 40px;
}

.footer__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}

.footer__logo {
  width: 60px;
  margin-right: 25px;
}

.menu--footer {
  width: 650px;
  position: relative;
  margin: 0;

  & .menu__item {
    width: 160px;
    margin: 0 25px 25px;

    & .menu__link {
      font-size: 14px;
    }
  }
}

.menu--footer .popap-menu {
  display: block;
  width: 160px;
  padding: 0;
  margin-top: 40px;
}

.menu__item--footer {
  position: absolute;
  top: 60px;
  right: 78px;
}

.top-menu--footer {
  margin: 0;
}

.top-menu__social--footer {
  position: static;
  display: block;
  margin-top: 70px;
}

@media (max-width: 1200px) {
  .menu__item--footer {
    position: static;
  }
}

@media (max-width: $mobile-width) {
  .menu--footer {
    width: 650px;
    position: relative;
    margin: 0;
    justify-content: center;
    border-bottom: 1px solid $text-color;

    & .menu__item {
      width: 50%;
      margin: 0 0 25px;
      border: none;
    }

    & .popap-menu {
      width: auto;

      & .popap-menu__item {
        border: none;
        padding: 0 0 20px;
      }
    }
  }

  .top-menu__social--footer {
    margin-top: 0;
  }
}
