.pay-form {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-bottom: 170px;
  background: url('../img/purchase.png') no-repeat bottom right;
}

.pay-form__label {
  font-size: 18px;
  text-transform: uppercase;
  margin-bottom: 15px;
}

.pay-form__input {
  width: 457px;
  max-width: 95%;
  min-height: 45px;
  background-color: $bg-color;
  margin-bottom: 35px;
  border: none;
  padding: 12px 18px;

  &:hover,
  &:focus,
  &:focus-visible,
  &:active {
    border: none;
    outline: none;
    background-color: rgba($prime-color, 0.1);
  }
}

.pay-form__button {
  margin-top: 50px;
}
