.page-list__main {
  background: url('../img/finance.png') no-repeat top right;
}

.page-list__title {
  width: 60%;
}

.page-list {
  list-style: none;
  padding: 0;
  margin-bottom: 70px;
}

.page-list--perehod {
  max-width: 500px;
}

.page-list__item {
  padding-left: 40px;
  position: relative;
  margin-bottom: 20px;

  &::before {
    content: "";
    width: 16px;
    height: 16px;
    position: absolute;
    top: 5px;
    left: 0;
    background-color: $prime-color;
  }

  &:nth-of-type(even)::before {
    background-color: $sec-color;
  }

  & h2 {
    font-size: 16px;
    font-weight: normal;
    text-decoration-line: underline;
    text-transform: uppercase;
  }
}

.page-list__name {
  font-size: 20px;
  margin-top: 0;
  font-weight: normal;
  text-decoration-line: underline;
  text-transform: uppercase;
}

.page-list-num {
  padding: 0;
  list-style: none;
  margin-top: 40px;

  & ul {
    list-style: none;
  }
}

.page-list-num__item {
  padding-left: 60px;
  margin-bottom: 25px;
  position: relative;

  &::before {
    content: "";
    width: 33px;
    height: 32px;
    display: block;
    position: absolute;
    top: -5px;
    left: 0;
    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;
  }

  &:first-of-type::before {
    background-image: url('../img/one1.png');
  }

  &:nth-of-type(2)::before {
    background-image: url('../img/two2.png');
  }

  &:nth-of-type(3)::before {
    background-image: url('../img/three.png');
  }

  &:nth-of-type(4)::before {
    background-image: url('../img/four.png');
  }
}

.list-page {
  list-style: none;
  padding: 0;
}

.list-page__item {
  padding: 30px 25px;
  margin-bottom: 10px;
  background: url(../img/over.png) no-repeat center/100% auto;
  display: inline-block;
}

.page-list__button {
  margin-bottom: 100px;
}

@media (max-width: $mobile-width) {
  .page-list__main {
    background-size: 50% auto;
    background-position: bottom -30px right;
  }

  .page-list__title {
    text-align: center;
    width: auto;
  }

  .page-list--perehod {
    margin-bottom: 0;
  }
}
