.perehod {
  background: url('../img/why.png') no-repeat top 100px right;
}

.perehod__accent {
  background-color: $bg-color;
  padding: 40px 0;
  margin-bottom: 70px;

  & .wrapper * {
    max-width: 770px;
  }
}

.perehod__title {
  margin: 0 0 20px;
  font-size: 20px;
  text-transform: uppercase;
}

.perehod__how {
  margin-bottom: 80px;

  & .wrapper * {
    max-width: 770px;
  }
}

.perehod__button {
  margin-top: 50px;
}

@media screen {
  .perehod {
    background-position: bottom right;
    background-size: 30% auto;
    padding-bottom: 100px;
  }
}
