@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }
  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-10px);
  }
  20%, 40%, 60%, 80% {
    transform: translateX(10px);
  }
}

.modal {
  position: fixed;
  background-color: #fff;
  max-height: 100%;
  max-width: 94%;
  width: 1040px;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%);
  z-index: 1010;
  padding: 50px 65px 30px;
  display: none;
}

body::after {
  content: "";
  display: block;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 0;
  display: none;
}

.modal-overlay-show::after {
  display: block;
}

.modal-error {
  animation: shake 0.6s;
}

.modal__close {
  position: absolute;
  top: 18px;
  right: 25px;
  width: 40px;
  height: 40px;
  font-size: 0;
  background-color: transparent;
  border: 0;
  outline: 0;
}
.modal__close::before, .modal__close::after {
  content: "";
  position: absolute;
  top: 10px;
  left: 15px;
  width: 20px;
  height: 2px;
  background-color: #323232;
}

.modal__close::before {
  transform: rotate(45deg);
}

.modal__close::after {
  transform: rotate(-45deg);
}

.modal__list {
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  justify-content: center;
  padding: 0;
  margin: 0 0 15px;
}

.modal__item {
  position: relative;
  width: 50%;
  padding: 0 20px 0 80px;
  margin-bottom: 15px;

  &::before {
    content: "";
    width: 56px;
    height: 52px;
    display: block;
    position: absolute;
    top: 5px;
    left: 0;
  }

  &--one::before {
    background: url('../img/one.png') no-repeat center/contain;
  }

  &--two::before {
    background: url('../img/two.png') no-repeat center/contain;
  }
}

.modal__form {
  width: 400px;
  max-width: 94%;
  margin: 0 auto;

  &--page {
    margin-bottom: 50px;
  }
}

.modal__label {
  font-size: 14px;
  color: $sec-color;
}

.modal__input {
  box-sizing: border-box;
  width: 100%;
  min-height: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #f5a800;
  padding: 6px 10px;
  font-size: 11px;
  margin-bottom: 30px;
  outline: 0;
}

.modal__input:hover {
  border-color: $sec-color;
}

.modal__input:active, .modal__input:focus {
  background-color: rgba($prime-color, 0.1);
}

.modal__input--file {
  margin-bottom: 10px;
  display: flex;
  justify-content: space-between;
  padding: 0;
}

.file {
  opacity: 0;
  order: -1;
  position: absolute;
}

.modal__label--file {
  display: flex;
  justify-content: space-between;
  width: 100%;
  align-items: center;

  &::before {
    content: "";
    width: 90px;
    height: 30px;
    display: block;
    margin-bottom: 10px;
    background: url('../img/download.png') no-repeat center/contain;
  }
}

.modal__prim {
  font-size: 14px;
  text-transform: lowercase;
  color: #6F6F6F;
  margin-bottom: 25px;
}

.modal__none {
  display: none;
}

.modal__checkbox {
  margin-bottom: 50px;
}

.modal__label-check {
  font-size: 14px;
  position: relative;
}

.modal__form input[type="checkbox"]:checked + label,
.modal__form input[type="checkbox"]:not(:checked) + label {
  display: inline-block;
  position: relative;
  padding-left: 35px;
  cursor: pointer;
}

.modal__form input[type="checkbox"] + label:before {
  content: "";
  position: absolute;
  left: 0;
  top: -4px;
  width: 24px;
  height: 24px;
  border: 4px solid $prime-color;
  border-radius: 5px;
}

.modal__form input[type="checkbox"]:checked + label:before {
  background-color: $prime-color;
}

.modal__form input[type="checkbox"]:checked  + label:after {
  content: "\2713";
  position: absolute;
  left: 5px;
  top: -4px;
  width: 20px;
  height: 20px;
  border: none;
  color: #fff;
  font-weight: bold;
  font-size: 20px;
}

.modal__button {
  margin: 0 auto;
  border: none;
}

@media (max-width: $mobile-width) {
  .modal__item {
    width: 400px;
    max-width: 95%;
  }
}
