.page__title {
  font-size: 42px;
  font-weight: normal;
  margin: 0;
  padding: 70px 0 30px;

  &--center {
    text-align: center;
  }
}

.page--calc {
  background-color: $sec-color;

  & .page__title {
    color: #f6f6f6;
  }
}

.page--faq {
  padding-bottom: 100px;
  background: url('../img/faq.png') no-repeat bottom right, url('../img/communication.png') no-repeat top right;
}

.no-page {
  background: $bg-color;
  display: flex;
  justify-content: center;
  align-items: center;
  align-content: center;
  height: 100vh;

   & .wrapper {
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    & > div {
      width: 50%;
    }
   }
}

.no-page__title {
  font-weight: normal;
  font-size: 42px;
  margin: 0 0 40px;
}

@media (max-width: 1060px) {
  .page--faq {
    background: none;

    & .page__title {
      text-align: center;
    }
  }
}

@media (max-width: $mobile-width) {
  .page__title {
    font-size: 30px;
    padding-top: 30px;
  }

  .no-page .wrapper > div {
    width: 100%;
    text-align: center;
  }

  .no-page__title {
    font-size: 30px;
  }
}
