.contacts {
  display: flex;
  margin-bottom: 90px;
}

.contacts__text {
  width: 45%;
  background: url('../img/delivery.png') no-repeat top right;
}

.contacts__adres {
  max-width: 50%;
}

.contacts__phone {
  color: $text-color;
  text-decoration: none;

  &::before {
    content: "";
    display: inline-block;
    vertical-align: middle;
    margin-right: 10px;
    width: 16px;
    height: 16px;
    background: url("../img/icon-phone.svg") no-repeat center/contain;
  }
}

.contacts__email {
  text-transform: uppercase;
  margin: 30px 0;
}

.contacts__title {
  color: #ffa000;
}

.contacts__comment {
  margin: 30px 0;
  width: 360px;
}

.contacts__map {
  width: 65%;
}

@media (max-width: $mobile-width) {
  .contacts {
    flex-wrap: wrap;
    margin-bottom: 30px;
  }

  .contacts__text,
  .contacts__map,
  .contacts__comment {
    width: 100%;
  }
}
