.profitability {
  background-color: $bg-color;
  padding: 70px 0 80px;
}

.profitability__title {
  font-weight: normal;
  font-size: 42px;
  text-align: center;
  margin: 0 0 30px;
}

.profitability__grafic-img-full {
  display: none;
}

.profitability__list,
.profitability__grafics {
  display: flex;
  justify-content: space-evenly;
  padding: 0;
  list-style: none;
}

.profitability__list-item {
  padding-left: 20px;
  margin-bottom: 40px;
  min-width: 260px;
  background: url("../img/check.svg") no-repeat top left;
}

.profitability__grafics {
}

.profitability__grafic-name {
  font-weight: bold;
  font-size: 30px;
  max-width: 295px;
  margin: 0 auto;
  text-align: center;
}

.profitability__grafic-text {
  font-weight: bold;
  font-size: 18px;
  text-align: center;
}

.profitability__grafic-discr {
  margin: 0;
  text-align: center;
  font-size: 14px;
}

.profitability__table {
  margin: 10px auto;
  border-collapse: collapse;
  font-size: 18px;
  min-width: 300px;

  & tr:nth-child(odd) {
    background: rgba($sec-color, 0.2);
  }

  & td {
    padding: 15px 10px;
    max-width: 300px;
    border-bottom: 2px solid $sec-color;
  }

  & tr:last-child td {
    border-bottom: none;
  }

  & td:nth-child(even) {
    text-align: center;
    font-weight: bold;
    border-left: 2px solid $sec-color;
  }
}

.profitability__grafic-lup {
  margin: 20px auto 0;
  width: 20px;
  height: 20px;
  background: url("../img/Shape.png");
  display: block;
}

.profitability__footer {
  text-align: center;
  margin-top: 20px;
}

.popup {
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  display: none;
  text-align: center;
}

.popup_bg {
  position: absolute;
  z-index: 1;
  height: 100%;
  width: 100%;
}

.popup_img {
  position: absolute;
  z-index: 2;
  max-height: 94%;
  max-width: 94%;
  top: 50%;
  left: 50%;
  margin-right: -50%;
  transform: translate(-50%, -50%)
}

.popap_close {
  position: absolute;
  width: 25px;
  height: 25px;

  top: 0;
  left: 0;
  background: url("../img/close.svg") no-repeat center/contain;
}

@media (max-width: $mobile-width) {
  .profitability__list {
    flex-wrap: wrap;
    margin-bottom: 10px;
  }

  .profitability__list-item {
    margin-bottom: 20px;
  }

  .profitability__grafics-wrapper {
    overflow-x: scroll;
  }

  .profitability__grafics {
    width: 650px;
  }

  .profitability__grafic {
    width: 320px;
    background: #fff;
    box-shadow: 4px 4px 12px rgba(0, 0, 0, 0.1);
    margin-right: 20px;
    padding: 30px 10px;
  }

  .profitability__grafic-name {
    font-size: 15px;
  }

  .profitability__grafic-text {
    font-size: 14px;

    &::before {
      width: 20px;
      height: 20px;
    }
  }

  .profitability__grafic-discr {
    font-size: 13px;
  }

  .profitability__footer {
    display: none;
  }
}
