.tabs {
  margin-bottom: 80px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.tab {
  display: inline-block;
  cursor: pointer;
  text-transform: uppercase;
  border-bottom: 4px solid transparent;
  margin: 0 10px;
  padding-bottom: 15px;
  position: relative;
  transition: 0.5s;

  &.active {
    font-weight: bold;
    border-color: $prime-color;
    padding-left: 30px;

    &::before {
      content: "";
      width: 16px;
      height: 16px;
      display: block;
      position: absolute;
      top: 2px;
      left: 0;
      background-color: $prime-color;
    }
  }
}

.tab-item {
  padding-left: 95px;

  &--three {
    background: url('../img/delivery_hand.png') no-repeat bottom right;
  }

  &--four {
    background: url('../img/communication_chat.png') no-repeat bottom right;
  }
}

.tab__text {
  max-width: 700px;

  &--upper {
    text-transform: uppercase;
  }

  & a {
    text-transform: uppercase;
  }
}

.tab__button {
  margin: 47px 0 100px;
}

.tab__table {
  border-collapse: collapse;
  margin-top: 45px;

  & td {
    padding: 0 50px 15px 0;
    max-width: 460px;

    &:nth-of-type(odd) {
      text-transform: uppercase;
      text-decoration: underline;
    }
  }
}
