.uslovia {
  background: url('../img/idea.png') no-repeat top right/35% auto;

  & > * {
    max-width: 60%;
  }
}

.uslovia__accent {
  background: $bg-color;
  padding: 60px 0;

  & .page-list-num {
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    max-height: 480px;
    align-content: space-between;
    background: url('../img/business_agreement.png') no-repeat center/30% auto;
  }

  & .page-list-num__item {
    max-width: 33%;
  }
}

.uslovia__title {
  text-align: center;
  font-size: 20px;
  text-transform: uppercase;
  margin: 0 auto 70px;
  max-width: 500px;
}

.uslovia__sec {
  padding: 70px 0;
  text-align: center;

  & .uslovia__title {
    margin-bottom: 0;
  }

  & .list-page {
    padding-top: 70px;
    margin-bottom: 70px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    align-items: flex-start;
    max-height: 370px;
    align-content: space-between;
    background: url('../img/lineez.png') no-repeat top 30px center/30% auto;
  }

  & .list-page__item:nth-of-type(2) {
    margin-left: 10%;
  }

  & .list-page__item:nth-of-type(3) {
    margin-left: 25%;
  }

  & .list-page__item:nth-of-type(5) {
    margin-left: -10%;
  }

  & .list-page__item:nth-of-type(6) {
    margin-left: -25%;
  }
}

@media (max-width: 1100px) {
  .uslovia__sec {
    & .list-page__item:nth-of-type(2) {
      margin-left: 5%;
    }

    & .list-page__item:nth-of-type(3) {
      margin-left: 15%;
    }

    & .list-page__item:nth-of-type(5) {
      margin-left: -5%;
    }

    & .list-page__item:nth-of-type(6) {
      margin-left: -15%;
    }
  }
}

@media (max-width: $mobile-width) {
  .uslovia {
    background-position: top right;
    background-size: 150px auto;

    & > * {
      max-width: 100%;
    }

    & > h1 {
      max-width: 80%;
    }
  }

  .uslovia__accent {
    padding: 40px 0 0;

    & .page-list-num {
      max-height: 100%;
      align-content: flex-end;
      background-position: bottom left;
      background-size: 200px auto;
      padding-bottom: 150px;
    }

    & .page-list-num__item {
      max-width: 90%;
    }
  }

  .uslovia__sec {

    & .list-page {
      padding-top: 30px;
      margin-bottom: 30px;
      max-height: 100%;
      background: none;
      align-content: center;
      align-items: center;
    }

    & .list-page__item:nth-of-type(2),
    & .list-page__item:nth-of-type(3),
    & .list-page__item:nth-of-type(5),
    & .list-page__item:nth-of-type(6) {
      margin-left: 0;
    }
  }
}
